// ---------------------------------------------------------
// COMPONENTA VIZUALĂ (NU TREBUIE SĂ MODIFICI MAI JOS)
// ---------------------------------------------------------

const Card = ({ i, title, description, tags, color, githubLink, liveLink, progress, range, targetScale }: any) => {
  const container = useRef(null);
  const { scrollYProgress } = useScroll({
    target: container,
    offset: ['start end', 'start start']
  });

  const imageScale = useTransform(scrollYProgress, [0, 1], [2, 1]);
  const scale = useTransform(progress, range, [1, targetScale]);

  return (
    <div ref={container} className="h-screen flex items-center justify-center sticky top-0">
      <motion.div 
        style={{ scale, top: `calc(-5vh + ${i * 25}px)` }} 
        className={`flex flex-col relative -top-[25%] h-[500px] w-[1000px] rounded-3xl p-10 origin-top border border-white/10 bg-gradient-to-br ${color} shadow-2xl overflow-hidden`}
      >
        <div className="flex h-full gap-10 relative z-10">
          
          {/* Stânga: Text și Linkuri */}
          <div className="w-[40%] flex flex-col justify-between">
            <div>
              <h2 className="text-4xl font-bold mb-4 text-white">{title}</h2>
              <p className="text-gray-300 text-lg leading-relaxed">{description}</p>
            </div>
            
            <div className="flex flex-wrap gap-2 mt-4">
              {tags.map((tag: string, idx: number) => (
                <span key={idx} className="px-3 py-1 bg-white/10 rounded-full text-xs font-mono text-blue-200 border border-white/5">
                  {tag}
                </span>
              ))}
            </div>

            <div className="mt-8 flex gap-4">
                {githubLink && (
                    <a href={githubLink} target="_blank" className="flex items-center gap-2 px-4 py-2 bg-black/30 rounded-lg hover:bg-black/50 transition-colors text-sm font-bold border border-white/10">
                        <Github className="w-4 h-4" /> Source Code
                    </a>
                )}
                {liveLink && liveLink !== "#" && (
                    <a href={liveLink} target="_blank" className="flex items-center gap-2 px-4 py-2 bg-white text-black rounded-lg hover:bg-gray-200 transition-colors text-sm font-bold">
                        <ExternalLink className="w-4 h-4" /> Live Demo
                    </a>
                )}
            </div>
          </div>

          {/* Dreapta: Vizual / Imagine */}
          <div className="relative w-[60%] h-full rounded-2xl overflow-hidden border border-white/5 bg-black/20 group">
            <motion.div 
              className="w-full h-full bg-gray-900/50 flex items-center justify-center"
              style={{ scale: imageScale }}
            >
               {/* Aici poți pune <img src="/poza-proiect.png" /> dacă ai poze */}
               {/* Momentan e un placeholder stilizat */}
               <div className="text-center p-10">
                   <div className="text-6xl mb-4 opacity-20">abstract://</div>
                   <div className="text-gray-500 font-mono text-sm">IMAGE_PLACEHOLDER_FOR_{i+1}</div>
               </div>
            </motion.div>
          </div>

        </div>
      </motion.div>
    </div>
  )
}

export default function Projects() {
  const container = useRef(null);
  const { scrollYProgress } = useScroll({
    target: container,
    offset: ['start start', 'end end']
  });

  return (
    <div ref={container} className="relative mt-[20vh] mb-[50vh] px-4">
        <div className="mb-32 px-4 text-center">
            <h2 className="text-5xl md:text-7xl font-bold text-white mb-6">Proiecte Recente</h2>
            <p className="text-gray-400 max-w-2xl mx-auto">
                O selecție de aplicații și experimente care demonstrează capacitatea mea de a rezolva probleme reale.
            </p>
        </div>
        
        <div className="max-w-[1200px] mx-auto">
            {projects.map((project, i) => {
                const targetScale = 1 - ( (projects.length - i) * 0.05);
                return (
                    <Card 
                      key={i} 
                      i={i} 
                      {...project} 
                      progress={scrollYProgress} 
                      range={[i * 0.25, 1]} 
                      targetScale={targetScale} 
                    />
                );
            })}
        </div>
    </div>
  );
}